@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

@media screen and (max-width: 500px) {

  html {
    font-size: 45%;
  }

}
.card {
    border-radius: 12px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}
.chart {
    height: 12rem;
    padding: 1rem;
    margin: 1rem;
    margin-bottom: 3rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    background: #444;
    border-radius: 10px;
}
.chart-bar {
    width: 1.7rem;
    height: 100%;
    display: flex;
    flex-direction: column;
    margin: 0 1rem;
}

.chart-bar__inner {
    height: 100%;
    width: 100%;
    background: #fff;
    border-radius: 50px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    overflow: hidden;
}

.chart-bar__fill {
    width: 100%;
    background: #ffc420;
    transition: all 0.3s ease-out;
}

.chart-bar__label {
    color: #fff;
    font-weight: 600;
    font-size: 0.8rem;
}

.expense-filter {
    padding: 2rem 1.2rem;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.expense-filter__menu {
    color: #333;
    background: #fff;
    cursor: pointer;
    padding: 0.5rem 1rem;
    font-size: 1.1rem;
    outline: none;
}

.expense-item__date {
    background: #444;
    width: 8rem;
    border: 3px solid #fff;
    border-radius: 10px;
    padding: 1rem 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.expense-item__date > div:last-child {
    font-size: 2rem;
}
.expense-item {
    padding: 1.2rem 2rem;
    background: #333;
    color: #fff;
    display: flex;
    align-items: center;
    margin: 1rem;
}


.expense-item__description {
    margin-left: 3rem;
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.expense-item__price {
    font-size: 1.3rem;
    padding: 1.4rem;
    width: 10rem;
    text-align: center;
    border: 3px solid #fff;
    border-radius: 10px;
    background: #ffc420;
}
.expenses {
  padding: 1rem;
  background-color: rgb(31, 31, 31);
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
}

h2 {
  color: #fff;
  text-align: center;
  margin-bottom: 2rem;
}

.new-expense {
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
}

.new-expense__controls {
    padding: 1rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.new-expense__control {
    display: flex;
    flex-direction: column;
    width: 80%;
}

.new-expense__actions {
    margin: 0 1rem;
    margin-bottom: 2rem;
}

.new-expense__actions button {
    margin-right: 2rem;
}


.new-expense__open {
    display: flex;
    align-items: center;
    justify-content: center;
}

label {
    font-size: 1.3rem;
    margin-bottom: 0.6rem;
}

input {
    outline: none;
    margin-bottom: 2rem;
    height: 3rem;
    font-size: 1.2rem;
    padding: 1rem 0.5rem;
}

button {
    cursor: pointer;
    background: transparent;
    color: #333;
    border: 3px solid #333;
    border-radius: 10px;
    font-size: 1.2rem;
    font-weight: 600;
    padding: 1rem 2rem;
    transition: all 0.4s ease;
}

button:hover {
    background: #333;
    color: #fff;
}


.new-expense__actions button:last-child {
    background: #333;
    color: #fff;
}

.new-expense__actions button:last-child:hover {
    opacity: 0.8;
}
